
#all-packages {
    margin-top: 5vh;
}

.package-header {
    height: auto;
    /* border-bottom: 1px solid black; */
}

#all-packages h4,#all-packages h5,#all-packages p {
    margin: 0px;
    padding: 0px;
}

.package-header h4 {
    font-weight: 600;
}

#filter-container {
    height: 10vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media(max-width:700px){
    .package-header {
        height: auto;
    }

    #filter-container {
        margin-top: 8px;
        margin-bottom: 8px;
        height: auto;
    }

}


/* packages */
.packages {
    margin-top: 5vh;
}

.packages .row, .packages .col {
    padding: 0px;
}

.package-item {
    /* height: 30vh; */
    /* border-radius: 12px;
    border: 0.1px solid rgb(172, 172, 172); */
    padding: 0px;
    /* overflow: hidden; */
}

.package-data {
    height: 25vh;
    /* border: 0.1px solid rgb(172, 172, 172); */
    border-radius: 12px;
    overflow: hidden;
}

.package-image {
    margin: 0px;
    padding: 0px;
    height: 30vh;
    object-fit: cover;
    width: 100%;
}

.header {
    border-bottom: 0.1px solid rgb(172, 172, 172);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h6{
    color: black;
    font-weight: 600;
}

.header h6:hover {
    color: var(--primaryColor);
}

.header > h6 {
    border-left: 0.1px solid rgb(172, 172, 172);
    color: rgb(71, 71, 71);
    font-weight: normal;
    font-size: 14px;
    padding: 0px 20px 0px 10px; 
}

.package-item-content {
    padding-left: 16px !important;
}

.path {
    padding: 5px 0px;
    border-bottom: 0.1px solid rgb(172, 172, 172);
}

.path p {
    font-size: 14px;
}

.inclusions {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    /* justify-content: space-between; */
}

.inclusion {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left: 0px !important;
    margin-right: 24px;
}

.inclusion span {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    border: 0.4px solid rgb(172, 172, 172);
    color: var(--primaryColor);
}

.inclusion p{
    font-size: 12px;
    margin-top: 5px !important;
}

.inclusion span:hover {
    color: white;
    background: var(--secondaryColor);
}

.tour-item-book {
    height: 25vh;
    /* border: 0.4px solid rgb(172, 172, 172); */
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.tour-item-book .data h5{
    font-weight: bold;
}

.tour-item-book .data p{
    font-size: 12px;
}

.tour-item-book .data > div {
    display: flex;
    justify-content: space-between;
    margin-top: 12px;
}

.tour-item-book .data button {
    cursor: pointer;
    width: 118px;
    font-size: 12px;
    border: none;
    outline: none;
    text-decoration: none;
    padding: 10px 20px;
    background: var(--primaryColor);
    color: white;
}

.tour-item-book .data button:nth-child(1){
    margin-right: 5px;
    background: whit
    e;
    border: 1px solid var(--primaryColor);
    color: var(--primaryColor);
}

.tour-item-book .data button:nth-child(2):hover{
    background: #038dd8;
}

.tour-item-book .data button:nth-child(1):hover {
    background: var(--primaryColor);
    color: white;
}

@media(max-width:700px){

    .package-item {
        /* border-bottom: 0.4px solid rgb(172, 172, 172); */
        border-radius: 12px;
    }

    .package-data {
        border: none;
        height: auto;
    }

    .inclusions {
        width: 100%;
        margin-top: 0px;
        margin-bottom: 24px;
    }

    .inclusion {
        margin-top: 8px;
    }

    .tour-item-book {
        height: 20vh;
    }

}

#load-more {
    background: var(--primaryColor);
    padding: 10px 20px;
    color: white;
    border-radius: 8px;
    border: none;
    cursor: pointer;
}


#load-more:hover {
    background: #038dd8;
}